export default {
	data() {
		return {
			show: false,
			username: '',
			password: '',
			repeat: '',
			code: '',
			qiye: true,
			geren: false,
			msg: '1',
			ra: 'no',
			tishi: '',
			baseUrl: '',
			showText:true,
			second:59,
			disabled:false,
			changess:true
		};
	},
	onShow() {
		uni.setNavigationBarTitle({
			title: this.$t('index.zhuce')
		})	
		// console.log('now:' + this.websiteUrl);
		this.baseUrl = this.websiteUrl;
		// console.log(this.baseUrl);
	},
	computed: {
		i18n() {
			return this.$t('index');
		}
	},
	methods: {
		changeLangu() {
			console.log('语言切换');
			const system_info = uni.getStorageSync('system_info');
			system_info.language === 'en' ? (system_info.language = this._i18n.locale = 'zh_CN') : (system_info.language = this._i18n.locale = 'en');
			uni.setStorageSync('system_info', system_info);
			uni.reLaunch({
				url: 'index'
			});
			if(system_info.language === 'en'){
				uni.setNavigationBarTitle({
					title: this.$t('index.zhuce')
				})
			
			}else {
				uni.setNavigationBarTitle({
					title: this.$t('index.zhuce')
				})
			
			}
		},
		messagesearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.username = event.target.value;
		},
		passwordsearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.password = event.target.value;
		},
		codesearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.code = event.target.value;
		},
		repeatsearch: function(event) {
			// 这里动态获取VALUE 值
			// console.log(event.target.value)
			this.repeat = event.target.value;
		},
		change() {
			this.show = !this.show;
			if (this.show == true) {
				this.ra = 'yes';
			} else {
				this.ra = 'no';
			}
		},
		gerenClick() {
			console.log(this.geren);
			this.geren = true;
			this.qiye = false;

			this.msg = '2';
		},
		qiyeClick() {
			console.log(this.qiye);
			this.geren = false;
			this.qiye = true;
			this.msg = '1';
		},
		// 获取验证码
		codeClick() {
			let username = this.username;
			let reg = new RegExp('^[a-z0-9A-Z]+[- | a-z0-9A-Z . _]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\\.)+[a-z]{2,}$');
			console.log('长度' + username.length);
			if (username == '' || username == null || username == undefined) {
				uni.showToast({
					title: '请输入账号',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (username.length == '11') {
				if (!/^1[3456789]\d{9}$/.test(username)) {
					uni.showToast({
						title: '请输入正确的手机号',
						icon: 'none',
						duration: 800
					});
					return false;
				}
			} else if (!reg.test(username)) {
				uni.showToast({
					title: '邮箱格式不正确，请重新输入！',
					icon: 'none',
					duration: 800
				});
				return false;
			}
			console.log(username);
			uni.request({
				url: this.baseUrl + '/api/code',
				method: 'post',
				data: {
					account: username,
					type: 'register'
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res.data);
					// this.tishi = res.data.msg
					if (res.data.code == 200) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
						this.changess = false;
						this.disabled = true
						// console.log("1")
						   this.showText = false
						   var interval = setInterval(() => {
						      --this.second
						   }, 1000)
						   
						   setTimeout(() => {
						      clearInterval(interval)
						      this.showText = true
							  this.second = 59
							  this.disabled = false
							  this.changess = true;
						   }, 60000)
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 800
						});
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 注册按钮
		login() {
			console.log(this.username);
			let username = this.username;
			let password = this.password;
			let repeat = this.repeat;
			let code = this.code;
			let agree = this.ra;
			let company = this.msg;

			console.log('同意或不同意 = ' + agree);
			console.log('企业/个人 = ' + this.msg);
			if (username == '' || username == null || username == undefined) {
				uni.showToast({
					title: '请输入账号',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (code == '' || code == null || code == undefined) {
				uni.showToast({
					title: '请输入验证码',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (password == '' || password == null || password == undefined) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (repeat == '' || repeat == null || repeat == undefined) {
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (repeat != password) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (agree == 'no') {
				uni.showToast({
					title: '您还没有同意隐私和协议',
					icon: 'none',
					duration: 800
				});
				return false;
			} else if (agree == 'yes') {
				uni.request({
					url: this.baseUrl + '/api/register',
					method: 'post',
					data: {
						is_company: company,
						account: username,
						password: password,
						confirm_password: repeat,
						code: code
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						console.log(res.data);
						if (res.data.code == 200) {
							uni.showToast({
								title: res.data.msg,
								icon: 'none',
								duration: 800
							});
							this.username = '';
							this.password = '';
							this.repeat = '';
							this.code = '';
						} else {
							uni.showToast({
								title: res.data.msg,
								icon: 'none',
								duration: 800
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});

				// 上一个页面
				// uni.navigateBack();
				// 首页
				// uni.switchTab({
				//          url: '../index/index'
				// });
			}
		},
		zhuce() {
			uni.navigateTo({
				url: '../login/login'
			});
		},
		yinsi() {
			uni.navigateTo({
				url: '../yinsizhengce/yinsizhengce'
			});
		},
		xieyi() {
			uni.navigateTo({
				url: '../yonghuxieyi/yonghuxieyi'
			});
		}
	}
};
